<template>
  <div class="new">
    <div class="header2">
      <span><img src="../assets/image/back.png" @click="back"></span>
      <div class="top2"></div>
    </div>
    <div class="titzhinan"><div class="greendiv"></div></div>
    <div class="page-loadmore">
      <mt-loadmore  :bottom-method="loadBottom" @bottom-status-change="handleBottomChange" :bottom-all-loaded="allLoaded" ref="loadmore">
        <ul class="kc_part">
          <router-link v-for="(aa,index) in kc" :to="{name:'Article',params:{kc,index}}">
            <li v-if="index<num"><p class="gongluep">{{aa.tit}}</p></li>
          </router-link>
        </ul>
      </mt-loadmore>
    </div>
  </div>
</template>

<style>
</style>

<script type="text/babel">
  export default {
    data() {
      return {
        kc:[
          {tit:'  ',text:'&nbsp&nbsp&nbsp&nbsp<br/>&nbsp&nbsp&nbsp&nbsp<br/>&nbsp&nbsp&nbsp&nbsp'},
          {tit:'  ',text:'&nbsp&nbsp&nbsp&nbsp<br/>&nbsp&nbsp&nbsp&nbsp'},
          {tit:' ',text:'&nbsp&nbsp&nbsp&nbsp_<br/>&nbsp&nbsp&nbsp&nbsp20'},
          {tit:'',text:'&nbsp&nbsp&nbsp&nbsp_<br/><br/>&nbsp&nbsp&nbsp&nbsp 20051134'},
          {tit:' ',text:'&nbsp&nbsp&nbsp&nbsp_51994<br/>&nbsp&nbsp&nbsp&nbsp3005050.883'},
          {tit:'  ',text:'&nbsp&nbsp&nbsp&nbsp_<br/>&nbsp&nbsp&nbsp&nbsp108km100km'},
          {tit:'  ',text:'&nbsp&nbsp&nbsp&nbsp_<br/>&nbsp&nbsp&nbsp&nbsp--<br/>&nbsp&nbsp&nbsp&nbsp--<br/>&nbsp&nbsp&nbsp&nbsp--'},
          {tit:'  ',text:'&nbsp&nbsp&nbsp&nbsp <br/>&nbsp&nbsp&nbsp&nbsp 40'},
          {tit:'   ',text:'&nbsp&nbsp&nbsp&nbsp 278<br/>&nbsp&nbsp&nbsp&nbsp '},
          {tit:'  ',text:'&nbsp&nbsp&nbsp&nbsp<br/>&nbsp&nbsp&nbsp&nbspXD4DDIYDIYDIY'},
          {tit:'  ',text:'&nbsp&nbsp&nbsp&nbsp<br/>&nbsp&nbsp&nbsp&nbsp'},
          {tit:' ',text:'&nbsp&nbsp&nbsp&nbsp_<br/>&nbsp&nbsp&nbsp&nbsp20'},
          {tit:'',text:'&nbsp&nbsp&nbsp&nbsp_<br/>&nbsp&nbsp&nbsp&nbsp<br/>&nbsp&nbsp&nbsp&nbsp 20051134'},
          {tit:'  ',text:'&nbsp&nbsp&nbsp&nbsp_<br/>&nbsp&nbsp&nbsp&nbsp108km100km'}

        ],
        num:14,
        allLoaded: false,
        bottomStatus: '',
        time:''
      };
    },
    methods: {
      handleBottomChange(status) {
        this.bottomStatus = status;
      },
      loadBottom() {
        clearTimeout(this.time)
        this.time=setTimeout(() => {
          if (this.num < 1000) {
            this.num=this.num+14;
            for (let i = 0; i <14; i++) {
              this.kc.push(this.kc[i]);
            }
          } else {
            this.allLoaded = true;
          }
          this.$refs.loadmore.onBottomLoaded();
        }, 1000);
      },
      back(){
        this.$router.go(-1)
      }
    }
  };
</script>



