<template>
  <div class="new">
    <div class="header2">
      <span><img src="../assets/image/back.png" @click="back"></span>
      <div class="top2"></div>
    </div>
    <img src="../assets/image/jingwai.png" width="100%">
    <div class="page-loadmore">
      <mt-loadmore  :bottom-method="loadBottom" @bottom-status-change="handleBottomChange" :bottom-all-loaded="allLoaded" ref="loadmore">
        <ul class="kc_part">
          <router-link v-for="(aa,index) in kc" :to="{name:'Attractions',params:{kc,index}}">
            <li v-if="index<num"><div style="width: 100px; height: 100px; margin-right: 10px;"><img :src="aa.logo" width="100px"  height="100px"></div><div class="text"><h3>{{aa.title}}</h3><p>{{aa.text}}</p></div></li>
          </router-link>
        </ul>
      </mt-loadmore>
    </div>
  </div>
</template>

<style>
</style>

<script type="text/babel">
  export default {
    data() {
      return {
        kc:[
          {title:'',logo:require('../assets/image/riben.jpg'),text:'4,00080%66% .'},
          {title:'',logo:require('../assets/image/xinjiapo.jpg'),text:' 12000'},
          {title:'',logo:require('../assets/image/bingdao.jpg'),text:'8000 3/4 400-8001/8100302119 227 MyvatnTingvellir(Skaftafell)(Svartifoss)100'},
          {title:'',logo:require('../assets/image/maerdaifu.jpg'),text:' 1200202 1.250'},
          {title:'',logo:require('../assets/image/taiguo.jpg'),text:'513,115 60,400,000  30,OOO90    '},
          {title:'',logo:require('../assets/image/yidali.jpg'),text:'  '}
        ],
        num:6,
        allLoaded: false,
        bottomStatus: '',
        time:''
      };
    },
    methods: {
      handleBottomChange(status) {
        this.bottomStatus = status;
      },
      loadBottom() {
        clearTimeout(this.time)
        this.time=setTimeout(() => {
          if (this.num < 40) {
            this.num=this.num+6;
            for (let i = 0; i <6; i++) {
              this.kc.push(this.kc[i]);
            }
          } else {
            this.allLoaded = true;
          }
          this.$refs.loadmore.onBottomLoaded();
        }, 1000);
      },
      back(){
        this.$router.go(-1)
      }
    }
  };
</script>



